import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    { path: '/', name: 'home', component: require('@/components/Home').default },
    { path: '/what', name: 'what', component: require('@/components/What').default },
    { path: '/history', name: 'history', component: require('@/components/History').default },
    { path: '/helloword', name: 'helloword', component: require('@/components/Helloword').default },
    { path: '/event', name: 'event', component: require('@/components/Event').default },
  ]
})



