<template>
  <div class="event">
    <div class="list-group custom" v-for="e in eventsOpen" :key="e._id">
      <a href="#" class="list-group-item list-group-item-action flex-column align-items-start active">
        <div class="d-flex w-100 justify-content-between">
          <h5 class="mb-1">{{ e.name }}</h5>
          <small>{{ e.date.slice(0,10)}}</small>
        </div>
        <p class="mb-1" v-html="e.description"></p>
        <small>{{ e.venue }}</small>
      </a>
    </div>
  </div>
</template>

<script>
import eventService from '@/service/eventService'

export default {
  data () {
    return {
      loading : false,
      eventsOpen: []
    }
  },
  async mounted () {
    this.loading = true
    this.eventsOpen = (await eventService.getEventOpen()).data
    this.loading = false
  }
}
</script>

<style>
  .event {
    margin-top:65px;
  }
  .custom {
    padding-bottom: 20px; 
  }
</style>


