<template>
  <div>
    <nav class="navbar navbar-expand-lg navbar-light bg-light fixed-top">

      <router-link :to="{ name: 'home'}" class="navbar-brand">
        <img src="static/profile.png" width="30" height="30" class="d-inline-block align-top" alt="logo">
        Benediktus
      </router-link>

      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavAltMarkup" aria-controls="navbarNavAltMarkup" aria-expanded="false" aria-label="Toggle navigation">
        <span class="navbar-toggler-icon"></span>
      </button>

      <div class="collapse navbar-collapse" id="navbarNavAltMarkup">
        <div class="navbar-nav">
          <router-link :to="{ name: 'what' }" :class="routerLink">What</router-link>
          <router-link :to="{ name: 'history' }" :class="routerLink">History</router-link>
          <router-link :to="{ name: 'helloword' }" :class="routerLink">HelloHord</router-link>
          <router-link :to="{ name: 'event' }" :class="routerLink">Event</router-link>
        </div>
      </div>
    </nav>
    
    <div>
      <router-view></router-view>
    </div>

  </div>
</template>

<script>
export default {
  name: 'App',

  data() {
    return {
      routerLink: ['nav-item', 'nav-link']
    }
  }
}
</script>

<style scoped>
a.lead {
  text-decoration: none;
  color: #333;
}

nav div a {
  border: 1px solid transparent;
  color: #8c8c8c;
}

nav div a:hover,
nav div a.router-link-active {
  color: white;
  background-color: #47b784;
  border: 1px solid #47b784;
}

/* nav {
  overflow: auto;
  height: 100vh;
} */
</style>



